import React, { useState, useEffect } from 'react'
import useScanSimulatorStore from '../stores/scanSimulatorStore'
import { useNotification } from '../contexts/NotificationContext'
import '../styles/forms.css'

const ScanSimulator = () => {
  const {
    scannedBarcodes,
    processedCount,
    processOnline,
    clearScannedBarcodes,
    getProcessedCount
  } = useScanSimulatorStore()

  const [manualBarcode, setManualBarcode] = useState('')
  const [manualError, setManualError] = useState('')
  const [expandedStep, setExpandedStep] = useState(null)
  const [isMobile, setIsMobile] = useState(false)
  
  // Tümünü Temizle modal state'leri
  const [showClearModal, setShowClearModal] = useState(false)
  const [clearScope, setClearScope] = useState('full')
  const [clearRange, setClearRange] = useState('24h')
  const [clearConfirmation, setClearConfirmation] = useState('')
  const [isClearing, setIsClearing] = useState(false)
  const [clearProgress, setClearProgress] = useState(0)
  const [clearStatus, setClearStatus] = useState('')
  
  const { showSuccess, showError, showWarning, showInfo, showModal } = useNotification()

  // Mobile detection
  useEffect(() => {
    const checkMobile = () => {
      setIsMobile(window.innerWidth <= 768)
    }
    
    checkMobile()
    window.addEventListener('resize', checkMobile)
    
    return () => window.removeEventListener('resize', checkMobile)
  }, [])

  // Test barkodları - Geçerli/geçersiz örnekler
  const testBarcodes = [
    { label: 'ULD-AKE12345AB', value: 'ULD-AKE12345AB', status: 'Geçerli', valid: true },
    { label: 'ULD-BKE67890CD', value: 'ULD-BKE67890CD', status: 'Geçerli', valid: true },
    { label: 'ULD-CKE11111EF', value: 'ULD-CKE11111EF', status: 'Geçersiz', valid: false },
    { label: 'ULD-DKE22222GH', value: 'ULD-DKE22222GH', status: 'Geçersiz', valid: false }
  ]

  // Barkod doğrulama
  const validateBarcode = (barcode) => {
    const cleanBarcode = barcode.trim().toUpperCase()
    const uldRegex = /^ULD-[A-Z]{3}\d{5}[A-Z]{2}$/
    const awbRegex = /^\d{3}-\d{8}$/
    const testRegex = /^TEST-\d{5}$/
    const uldTestRegex = /^ULD-TEST\d+$/
    return uldRegex.test(cleanBarcode) || awbRegex.test(cleanBarcode) || testRegex.test(cleanBarcode) || uldTestRegex.test(cleanBarcode)
  }

  // Barkod tarama simülasyonu - Web Online Mod (Direkt işleme)
  const simulateScan = (barcode) => {
    if (!validateBarcode(barcode)) {
      showError('Geçersiz barkod — format kurallarıyla uyuşmuyor', 'Geçersiz Barkod')
      return
    }

    // Direkt işle - Web her zaman online
    processOnline(barcode)
    showSuccess(`Barkod işlendi — ${barcode}`, 'Başarılı')
  }

  // Manuel barkod test - Web Online Mod
  const handleManualTest = () => {
    if (!manualBarcode.trim()) {
      setManualError('Lütfen bir barkod girin')
      return
    }

    if (!validateBarcode(manualBarcode)) {
      setManualError('Format geçersiz — örn: ULD-AKE12345AB')
      showError('Geçersiz barkod — format kurallarıyla uyuşmuyor', 'Geçersiz Barkod')
      return
    }

    // Manuel barkodu işle - Web her zaman online
    processOnline(manualBarcode)
    setManualBarcode('')
    setManualError('')
    showSuccess(`Barkod işlendi — ${manualBarcode}`, 'Başarılı')
  }

  // Enter tuşu ile çalıştırma
  const handleKeyPress = (e) => {
    if (e.key === 'Enter') {
      handleManualTest()
    }
  }

  // Klavye erişimi için accordion handler
  const handleAccordionKeyDown = (e, stepNumber) => {
    if (e.key === 'Enter' || e.key === ' ') {
      e.preventDefault()
      setExpandedStep(expandedStep === stepNumber ? null : stepNumber)
    } else if (e.key === 'ArrowDown') {
      e.preventDefault()
      const nextStep = stepNumber < 5 ? stepNumber + 1 : 1
      setExpandedStep(nextStep)
      // Focus next accordion header
      setTimeout(() => {
        const nextButton = document.querySelector(`[aria-controls="step-${nextStep}-content"]`)
        if (nextButton) nextButton.focus()
      }, 0)
    } else if (e.key === 'ArrowUp') {
      e.preventDefault()
      const prevStep = stepNumber > 1 ? stepNumber - 1 : 5
      setExpandedStep(prevStep)
      // Focus previous accordion header
      setTimeout(() => {
        const prevButton = document.querySelector(`[aria-controls="step-${prevStep}-content"]`)
        if (prevButton) prevButton.focus()
      }, 0)
    }
  }

  // Tümünü Temizle modalını aç
  const openClearModal = () => {
    const processedCount = getProcessedCount()
    
    if (processedCount === 0) {
      showInfo('Temizlenecek veri yok.', 'Sistem Boş')
      return
    }
    
    setShowClearModal(true)
    setClearConfirmation('')
    setClearScope('full')
    setClearRange('24h')
  }

  // Temizleme işlemini başlat
  const startClearing = async () => {
    if (clearConfirmation !== 'TEMİZLE') {
      showError('Lütfen "TEMİZLE" yazın', 'Onay Gerekli')
      return
    }

    setIsClearing(true)
    setClearProgress(0)
    setClearStatus('Kuyruk temizleniyor...')

    try {
      // Tek kuyruk temizleme işlemi
      const steps = []
      
      switch (clearScope) {
        case 'queue':
          steps.push(
            { status: 'PENDING görevleri temizleniyor...', progress: 50 },
            { status: 'PROCESSING görevleri temizleniyor...', progress: 100 }
          )
          break
        case 'queue+results':
          steps.push(
            { status: 'Kuyruk temizleniyor...', progress: 50 },
            { status: 'Sonuçlar siliniyor...', progress: 100 }
          )
          break
        case 'full':
          steps.push(
            { status: 'Kuyruk temizleniyor...', progress: 25 },
            { status: 'Sonuçlar siliniyor...', progress: 50 },
            { status: 'Loglar temizleniyor...', progress: 75 },
            { status: 'Metrikler sıfırlanıyor...', progress: 100 }
          )
          break
      }

      for (const step of steps) {
        setClearStatus(step.status)
        setClearProgress(step.progress)
        await new Promise(resolve => setTimeout(resolve, 800))
      }

      // Gerçek temizleme işlemi
      clearScannedBarcodes()
      
      // Modal'ı kapat
      setShowClearModal(false)
      setIsClearing(false)
      setClearProgress(0)
      setClearStatus('')
      setClearConfirmation('')
      
      showSuccess('Temizleme tamamlandı.', 'Başarılı')
      
    } catch (error) {
      console.error('Temizleme hatası:', error)
      showError('Temizleme sırasında hata oluştu. Lütfen tekrar deneyin.', 'Hata')
      setIsClearing(false)
      setClearProgress(0)
      setClearStatus('')
    }
  }

  // Modal'ı kapat
  const closeClearModal = () => {
    if (isClearing) return // İşlem sırasında kapatılamaz
    setShowClearModal(false)
    setClearConfirmation('')
    setClearProgress(0)
    setClearStatus('')
  }

  return (
    <div className="awb-form-container">
      <div className="form-container">
        {/* Professional Form Header - DG stili */}
        <div className="form-header">
          <h1 className="form-title">Barkod Simülasyonu</h1>
          <p className="form-subtitle">Test barkodları üretin ve sonuçları görün</p>
        </div>
        
        {/* Kontrol Paneli - Web Online Mod */}
        <div className="form-section">
          <div className="section-header">
            <h3 className="section-title">KONTROL PANELİ</h3>
          </div>
          
          {/* Aksiyon Butonları - Ortalı */}
          <div className="form-row form-row-centered">
            <div className="form-group">
              <button
                type="button"
                onClick={() => {
                  const testBarcode = 'ULD-TEST' + Date.now()
                  simulateScan(testBarcode)
                }}
                className="btn btn-primary"
                data-testid="scan-barcode-btn"
              >
                Barkod Tara / Barkod Ekle
              </button>
            </div>
            
            <div className="form-group">
              <button
                type="button"
                onClick={openClearModal}
                className="btn btn-danger"
                data-testid="clear-all-btn"
              >
                Tümünü Temizle
              </button>
            </div>
          </div>

          {/* Sayaç Kartı - Tek Kart Ortalı */}
          <div className="form-row form-row-centered">
            <div className="counter-display">
              <div className="counter-number">{getProcessedCount()}</div>
              <div className="counter-label">Toplam</div>
            </div>
          </div>
        </div>

        {/* Test Barkodları - Web Grid Yapısı */}
        <div className="form-section">
          <div className="section-header">
            <h3 className="section-title">TEST BARKODLARI</h3>
          </div>
          
          <div className="barcode-container">
            {/* Geçerli Barkodlar - Sol Sütun */}
            <div className="barcode-column">
              <div className="barcode-column-header">
                <h4 className="barcode-column-title">Geçerli Barkodlar</h4>
                <p className="barcode-column-description">Doğru format kurallarına uygun barkodlar</p>
              </div>
              <div className="barcode-grid">
                {testBarcodes.filter(b => b.valid).map((barcode, index) => (
                  <div
                    key={index}
                    className="barcode-card valid"
                    onClick={() => simulateScan(barcode.value)}
                    data-testid={`valid-barcode-${index}`}
                  >
                    <div className="barcode-badge">
                      ✓ Geçerli
                    </div>
                    <div className="barcode-label">{barcode.label}</div>
                    <div className="barcode-description">Format: ULD-XXX12345XX</div>
                  </div>
                ))}
              </div>
            </div>

            {/* Geçersiz Barkodlar - Sağ Sütun */}
            <div className="barcode-column">
              <div className="barcode-column-header">
                <h4 className="barcode-column-title">Geçersiz Barkodlar</h4>
                <p className="barcode-column-description">Format kurallarına uymayan barkodlar</p>
              </div>
              <div className="barcode-grid">
                {testBarcodes.filter(b => !b.valid).map((barcode, index) => (
                  <div
                    key={index}
                    className="barcode-card invalid"
                    onClick={() => simulateScan(barcode.value)}
                    data-testid={`invalid-barcode-${index}`}
                  >
                    <div className="barcode-badge">
                      ✗ Geçersiz
                    </div>
                    <div className="barcode-label">{barcode.label}</div>
                    <div className="barcode-description">Hatalı format örneği</div>
                  </div>
                ))}
              </div>
            </div>
          </div>
        </div>

        {/* Manuel Barkod Girişi - Web Stili */}
        <div className="form-section">
          <div className="manual-input-section">
            <div className="section-header">
              <h3 className="section-title">MANUEL BARKOD GİRİŞİ</h3>
            </div>
            
            <div className="form-row">
              <div className="form-group form-group-wide">
                <label className="form-label">Barkod Kodu</label>
                <div className="input-group">
                  <div className="input-row">
                    <div className="input-field">
                      <input
                        type="text"
                        value={manualBarcode}
                        onChange={(e) => {
                          setManualBarcode(e.target.value)
                          setManualError('')
                        }}
                        onKeyPress={handleKeyPress}
                        placeholder="Barkod girin (örn: ULD-AKE12345AB)"
                        className={`form-input ${manualError ? 'error' : ''}`}
                        data-testid="manual-barcode-input"
                      />
                    </div>
                    <button
                      type="button"
                      onClick={handleManualTest}
                      className="input-button"
                      data-testid="manual-test-btn"
                    >
                      Test Et
                    </button>
                  </div>
                  {manualError && (
                    <div className="form-error">{manualError}</div>
                  )}
                  <div className="form-help">
                    Geçerli formatlar: ULD-XXX12345XX, AWB-1234567890, TEST-12345
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        {/* Test Senaryoları - Geniş Accordion */}
        <div className="form-section">
          <div className="section-header">
            <h3 className="section-title">SENARYO ADIMLARI</h3>
          </div>
          
          <div className="form-row">
            <div className="form-group form-group-wide">
              <div className="scenario-accordion">
                <div className="accordion-item">
                  <button 
                    className="accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 1 ? null : 1)}
                    onKeyDown={(e) => handleAccordionKeyDown(e, 1)}
                    aria-expanded={expandedStep === 1}
                    aria-controls="step-1-content"
                  >
                    <div className="step-badge">1</div>
                    <div className="step-title">Barkod formatını öğrenin</div>
                    <div className="accordion-chevron">
                      {expandedStep === 1 ? '▲' : '▼'}
                    </div>
                  </button>
                  {expandedStep === 1 && (
                    <div id="step-1-content" className="accordion-content">
                      <p>Geçerli formatları inceleyin: ULD-ABC12345DE, 123-45678901, TEST-99999</p>
                    </div>
                  )}
                </div>

                <div className="accordion-item">
                  <button 
                    className="accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 2 ? null : 2)}
                    onKeyDown={(e) => handleAccordionKeyDown(e, 2)}
                    aria-expanded={expandedStep === 2}
                    aria-controls="step-2-content"
                  >
                    <div className="step-badge">2</div>
                    <div className="step-title">Bir barkod seçin veya girin</div>
                    <div className="accordion-chevron">
                      {expandedStep === 2 ? '▲' : '▼'}
                    </div>
                  </button>
                  {expandedStep === 2 && (
                    <div id="step-2-content" className="accordion-content">
                      <p>Test barkodlarından birini tıklayın veya manuel giriş yapın.</p>
                    </div>
                  )}
                </div>

                <div className="accordion-item">
                  <button 
                    className="accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 3 ? null : 3)}
                    onKeyDown={(e) => handleAccordionKeyDown(e, 3)}
                    aria-expanded={expandedStep === 3}
                    aria-controls="step-3-content"
                  >
                    <div className="step-badge">3</div>
                    <div className="step-title">Barkod anında işlenir</div>
                    <div className="accordion-chevron">
                      {expandedStep === 3 ? '▲' : '▼'}
                    </div>
                  </button>
                  {expandedStep === 3 && (
                    <div id="step-3-content" className="accordion-content">
                      <p>Web versiyonu online modda çalışır - barkodlar hemen işlenir ve sayaç artar.</p>
                    </div>
                  )}
                </div>

                <div className="accordion-item">
                  <button 
                    className="accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 4 ? null : 4)}
                    onKeyDown={(e) => handleAccordionKeyDown(e, 4)}
                    aria-expanded={expandedStep === 4}
                    aria-controls="step-4-content"
                  >
                    <div className="step-badge">4</div>
                    <div className="step-title">Sonuçları görüntüleyin</div>
                    <div className="accordion-chevron">
                      {expandedStep === 4 ? '▲' : '▼'}
                    </div>
                  </button>
                  {expandedStep === 4 && (
                    <div id="step-4-content" className="accordion-content">
                      <p>İşlenen barkod sayısını ve barkod listesini kontrol edin.</p>
                    </div>
                  )}
                </div>

                <div className="accordion-item">
                  <button 
                    className="accordion-header"
                    onClick={() => setExpandedStep(expandedStep === 5 ? null : 5)}
                    onKeyDown={(e) => handleAccordionKeyDown(e, 5)}
                    aria-expanded={expandedStep === 5}
                    aria-controls="step-5-content"
                  >
                    <div className="step-badge">5</div>
                    <div className="step-title">Temizleme işlemi</div>
                    <div className="accordion-chevron">
                      {expandedStep === 5 ? '▲' : '▼'}
                    </div>
                  </button>
                  {expandedStep === 5 && (
                    <div id="step-5-content" className="accordion-content">
                      <p>"Tümünü Temizle" ile tüm işlenmiş barkodları sıfırlayabilirsiniz.</p>
                    </div>
                  )}
                </div>
            </div>
            </div>
          </div>
        </div>
      </div>

      {/* Tümünü Temizle Modal */}
      {showClearModal && (
        <div className="modal-overlay" onClick={closeClearModal}>
          <div className="modal-container" onClick={(e) => e.stopPropagation()}>
            <div className="modal-header">
              <h2 className="modal-title">Tümünü Temizle</h2>
              <button 
                className="modal-close" 
                onClick={closeClearModal}
                disabled={isClearing}
                aria-label="Modal'ı kapat"
              >
                ×
              </button>
            </div>
            
            <div className="modal-body">
              <p className="modal-warning">Bu işlem geri alınamaz.</p>
              
              {/* Seçenekler */}
              <div className="clear-options">
                <label className="radio-option">
                  <input 
                    type="radio" 
                    name="clearScope" 
                    value="queue" 
                    checked={clearScope === 'queue'} 
                    onChange={(e) => setClearScope(e.target.value)}
                    disabled={isClearing}
                  />
                  <div className="option-content">
                    <div className="option-title">Sadece Kuyruk</div>
                    <div className="option-description">PENDING + PROCESSING görevleri sil.</div>
                  </div>
                </label>
                
                <label className="radio-option">
                  <input 
                    type="radio" 
                    name="clearScope" 
                    value="queue+results" 
                    checked={clearScope === 'queue+results'} 
                    onChange={(e) => setClearScope(e.target.value)}
                    disabled={isClearing}
                  />
                  <div className="option-content">
                    <div className="option-title">Kuyruk + Sonuçlar</div>
                    <div className="option-description">Kuyruk + tamamlanmış sonuç kayıtlarını sil.</div>
                  </div>
                </label>
                
                <label className="radio-option">
                  <input 
                    type="radio" 
                    name="clearScope" 
                    value="full" 
                    checked={clearScope === 'full'} 
                    onChange={(e) => setClearScope(e.target.value)}
                    disabled={isClearing}
                  />
                  <div className="option-content">
                    <div className="option-title">Tam Sıfırla</div>
                    <div className="option-description">Kuyruk + sonuç + log + metrik hepsini sil.</div>
                  </div>
                </label>
              </div>
              
              {/* Onay Checkbox */}
              <div className="clear-confirmation">
                <label className="checkbox-option">
                  <input 
                    type="checkbox" 
                    checked={clearConfirmation === 'TEMİZLE'} 
                    onChange={(e) => setClearConfirmation(e.target.checked ? 'TEMİZLE' : '')}
                    disabled={isClearing}
                  />
                  <span className="checkbox-label">Eminim, temizle.</span>
                </label>
              </div>
            </div>
            
            <div className="modal-footer">
              <button 
                type="button" 
                className="btn btn-secondary" 
                onClick={closeClearModal}
                disabled={isClearing}
              >
                İptal
              </button>
              <button 
                type="button" 
                className="btn btn-danger" 
                onClick={startClearing}
                disabled={clearConfirmation !== 'TEMİZLE' || isClearing}
              >
                {isClearing ? 'Temizleniyor...' : 'Temizle'}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}

export default ScanSimulator
