import React, { useState, useEffect } from 'react'
import './Toast.css'

const Toast = ({ 
  id, 
  type = 'info', 
  title, 
  message, 
  duration = 2500, // 2.5 saniye - hızlı geri bildirim 
  onClose, 
  action 
}) => {
  const [isVisible, setIsVisible] = useState(true)
  const [isHovered, setIsHovered] = useState(false)

  useEffect(() => {
    if (!isHovered) {
      const timer = setTimeout(() => {
        setIsVisible(false)
        setTimeout(() => onClose(id), 300) // CSS transition için
      }, duration)

      return () => clearTimeout(timer)
    }
  }, [duration, isHovered, id, onClose])

  // Hover durumunda timer'ı sıfırla
  useEffect(() => {
    if (isHovered) {
      // Hover'da timer'ı durdur
      return
    }
  }, [isHovered])

  const handleClose = () => {
    setIsVisible(false)
    setTimeout(() => onClose(id), 300)
  }

  const getIcon = () => {
    switch (type) {
      case 'success': return '✅'
      case 'warning': return '⚠️'
      case 'error': return '❌'
      case 'info': 
      default: return 'ℹ️'
    }
  }

  return (
    <div 
      className={`toast toast-${type} ${isVisible ? 'toast-visible' : 'toast-hidden'}`}
      onMouseEnter={() => setIsHovered(true)}
      onMouseLeave={() => setIsHovered(false)}
      role="alert"
      aria-live="polite"
    >
      <div className="toast-content">
        <div className="toast-icon">{getIcon()}</div>
        <div className="toast-text">
          <div className="toast-title">{title}</div>
          <div className="toast-message">{message}</div>
        </div>
        <button 
          className="toast-close" 
          onClick={handleClose}
          aria-label="Bildirimi kapat"
        >
          ×
        </button>
      </div>
      {action && (
        <div className="toast-action">
          <button 
            className="toast-action-btn"
            onClick={action.onClick}
          >
            {action.label}
          </button>
        </div>
      )}
    </div>
  )
}

export default Toast
