import React, { createContext, useContext, useState, useCallback, useRef, useEffect } from 'react'
import Toast from '../components/Toast'
import ConfirmModal from '../components/ConfirmModal'

const NotificationContext = createContext()

export const useNotification = () => {
  const context = useContext(NotificationContext)
  if (!context) {
    throw new Error('useNotification must be used within a NotificationProvider')
  }
  return context
}

export const NotificationProvider = ({ children }) => {
  const [toasts, setToasts] = useState([])
  const [modal, setModal] = useState(null)

  // Sayfa yenilendiğinde toast'ları temizle
  useEffect(() => {
    const handleBeforeUnload = () => {
      setToasts([])
      setModal(null)
    }

    window.addEventListener('beforeunload', handleBeforeUnload)
    return () => window.removeEventListener('beforeunload', handleBeforeUnload)
  }, [])

  // Toast işlemleri
  const showToast = useCallback((toast) => {
    const id = Date.now() + Math.random()
    const newToast = {
      id,
      type: 'info',
      duration: 3500, // 3.5 saniye - tutarlı süre
      ...toast
    }

    setToasts(prev => {
      // Aynı tip ve mesaj varsa güncelle
      const existingIndex = prev.findIndex(t => 
        t.type === newToast.type && 
        t.title === newToast.title && 
        t.message === newToast.message
      )
      
      if (existingIndex !== -1) {
        // Mevcut toast'ı güncelle
        const updated = [...prev]
        updated[existingIndex] = { ...newToast, id: updated[existingIndex].id }
        return updated
      }
      
      // Yeni toast ekle, maksimum 3 toast
      const updated = [newToast, ...prev].slice(0, 3)
      return updated
    })
  }, [])

  const removeToast = useCallback((id) => {
    setToasts(prev => prev.filter(toast => toast.id !== id))
  }, [])

  // Modal işlemleri
  const showModal = useCallback((modalConfig) => {
    setModal(modalConfig)
  }, [])

  const hideModal = useCallback(() => {
    setModal(null)
  }, [])

  // Hazır toast fonksiyonları
  const showSuccess = useCallback((message, title = 'Başarılı') => {
    showToast({ type: 'success', title, message })
  }, [showToast])

  const showWarning = useCallback((message, title = 'Uyarı') => {
    showToast({ type: 'warning', title, message })
  }, [showToast])

  const showError = useCallback((message, title = 'Hata') => {
    showToast({ type: 'error', title, message })
  }, [showToast])

  const showInfo = useCallback((message, title = 'Bilgi') => {
    showToast({ type: 'info', title, message })
  }, [showToast])

  // Debounce için
  const debouncedToasts = useRef({})
  
  const showDebouncedToast = useCallback((key, toast, delay = 500) => {
    if (debouncedToasts.current[key]) {
      clearTimeout(debouncedToasts.current[key])
    }
    
    debouncedToasts.current[key] = setTimeout(() => {
      showToast(toast)
      delete debouncedToasts.current[key]
    }, delay)
  }, [showToast])

  const value = {
    // Toast
    showToast,
    removeToast,
    showSuccess,
    showWarning,
    showError,
    showInfo,
    showDebouncedToast,
    
    // Modal
    showModal,
    hideModal,
    
    // State
    toasts,
    modal
  }

  return (
    <NotificationContext.Provider value={value}>
      {children}
      
      {/* Toast Container */}
      <div className="toast-container">
        {toasts.map(toast => (
          <Toast
            key={toast.id}
            {...toast}
            onClose={removeToast}
          />
        ))}
      </div>
      
      {/* Modal */}
      {modal && (
        <ConfirmModal
          {...modal}
          onClose={hideModal}
        />
      )}
    </NotificationContext.Provider>
  )
}

